import {
  ref
} from 'vue'
import {
  showLoadingToast,
  showFailToast,
  closeToast,
  showSuccessToast
} from 'vant'

/**
 * 分公司和中支数据管理器
 */
export class BranchDepartmentManager {
  constructor() {
    // 分公司数据
    this.branchOptions = ref([])
    this.branchOptionsBack = ref([])

    // 中支数据
    this.subBranchOption = ref([])

    // 弹窗状态
    this.showBranchPickerPopup = ref(false)
    this.showSubBranchPickerPopup = ref(false)
  }

  /**
   * 加载分公司数据
   */
  async loadBranchData() {
    return new Promise((resolve) => {
      setTimeout(() => {
        // 模拟分公司数据
        this.branchOptionsBack.value = [{
            "branchCode": "1",
            "branchName": "北京分公司",
            "id": "01",
            "itemList": [{
              "id": "0101",
              "subbranchCode": "10",
              "subbranchName": "北京本部"
            }]
          },
          {
            "branchCode": "2",
            "branchName": "湖北分公司",
            "id": "02",
            "itemList": [{
                "id": "0201",
                "subbranchCode": "10",
                "subbranchName": "武汉本部"
              },
              {
                "id": "0211",
                "subbranchCode": "20",
                "subbranchName": "宜昌中心支公司"
              },
              {
                "id": "0212",
                "subbranchCode": "30",
                "subbranchName": "襄阳中心支公司"
              },
              {
                "id": "0213",
                "subbranchCode": "50",
                "subbranchName": "十堰中心支公司"
              },
              {
                "id": "0214",
                "subbranchCode": "60",
                "subbranchName": "黄石中心支公司"
              },
              {
                "id": "0215",
                "subbranchCode": "40",
                "subbranchName": "荆州中心支公司"
              },
              {
                "id": "0216",
                "subbranchCode": "70",
                "subbranchName": "荆门中心支公司"
              },
              {
                "id": "0217",
                "subbranchCode": "A0",
                "subbranchName": "黄冈中心支公司"
              },
              {
                "id": "0218",
                "subbranchCode": "80",
                "subbranchName": "孝感中心支公司"
              },
              {
                "id": "0219",
                "subbranchCode": "90",
                "subbranchName": "咸宁中心支公司"
              },
              {
                "id": "0220",
                "subbranchCode": "D0",
                "subbranchName": "恩施中心支公司"
              },
              {
                "id": "0221",
                "subbranchCode": "C0",
                "subbranchName": "鄂州中心支公司"
              },
              {
                "id": "0222",
                "subbranchCode": "G0",
                "subbranchName": "天门中心支公司"
              },
              {
                "id": "0223",
                "subbranchCode": "F0",
                "subbranchName": "仙桃中心支公司"
              },
              {
                "id": "0224",
                "subbranchCode": "B0",
                "subbranchName": "随州中心支公司"
              },
              {
                "id": "0225",
                "subbranchCode": "K0",
                "subbranchName": "武汉本部业务管理二部"
              },
              {
                "id": "0226",
                "subbranchCode": "J0",
                "subbranchName": "武汉本部业务管理三部"
              },
              {
                "id": "0227",
                "subbranchCode": "E0",
                "subbranchName": "潜江中心支公司"
              }
            ]
          }
        ]

        // 转换为选择器格式
        this.branchOptions.value = this.branchOptionsBack.value.map(item => ({
          text: item.branchName,
          value: item.id
        }))

        resolve()
      }, 800)
    })
  }

  /**
   * 根据分公司代码加载中支数据
   * @param {string} branchCode - 分公司代码
   */
  loadSubBranchOptionByBranchCode(branchCode) {
    if (branchCode) {
      const branch = this.branchOptionsBack.value.find(item => item.id === branchCode)
      if (branch && branch.itemList) {
        this.subBranchOption.value = branch.itemList.map(item => ({
          text: item.subbranchName,
          value: item.id
        }))
      } else {
        this.subBranchOption.value = []
      }
    }
  }

  /**
   * 清空中支数据
   */
  clearSubBranchData() {
    this.subBranchOption.value = []
  }

  /**
   * 显示分公司选择器
   * @param {boolean} editable - 是否可编辑
   */
  async showBranchPicker(editable = true) {
    if (!editable) return

    // 显示加载状态
    showLoadingToast({
      message: '加载分公司数据...',
      forbidClick: true,
    })

    try {
      await this.loadBranchData()
      closeToast()
      this.showBranchPickerPopup.value = true
    } catch (error) {
      closeToast()
      showFailToast('加载失败，请重试')
      console.error('Failed to load branch data:', error)
    }
  }

  /**
   * 显示中支选择器
   * @param {boolean} editable - 是否可编辑
   */
  showSubBranchPicker(editable = true) {
    if (!editable) return
    this.showSubBranchPickerPopup.value = true
  }

  /**
   * 分公司选择器确认
   * @param {Object} params - 选择器参数
   * @param {Function} onBranchChange - 分公司变更回调
   */
  onBranchConfirm(params, onBranchChange) {
    const {
      selectedOptions
    } = params
    const selectBranch = selectedOptions[0]

    if (selectBranch && selectBranch.text) {
      // 清空中支数据并加载新的中支数据
      this.clearSubBranchData()
      this.loadSubBranchOptionByBranchCode(selectBranch.value)

      // 调用回调函数
      if (onBranchChange) {
        onBranchChange(selectBranch.value)
      }

      showSuccessToast(`已选择：${selectBranch.text}`)
    }

    this.showBranchPickerPopup.value = false
  }

  /**
   * 分公司选择器取消
   */
  onBranchCancel() {
    this.showBranchPickerPopup.value = false
  }

  /**
   * 中支选择器确认
   * @param {Object} params - 选择器参数
   * @param {Function} onSubBranchChange - 中支变更回调
   */
  onSubBranchConfirm(params, onSubBranchChange) {
    const {
      selectedOptions
    } = params
    const selectSubBranch = selectedOptions[0]

    if (selectSubBranch && selectSubBranch.text) {
      // 调用回调函数
      if (onSubBranchChange) {
        onSubBranchChange(selectSubBranch.value)
      }

      showSuccessToast(`已选择：${selectSubBranch.text}`)
    }

    this.showSubBranchPickerPopup.value = false
  }

  /**
   * 中支选择器取消
   */
  onSubBranchCancel() {
    this.showSubBranchPickerPopup.value = false
  }

  /**
   * 根据分公司代码获取分公司名称
   * @param {string} branchCode - 分公司代码
   * @returns {string} 分公司名称
   */
  getBranchNameByCode(branchCode) {
    const branch = this.branchOptionsBack.value.find(item => item.id === branchCode)
    return branch ? branch.branchName : ''
  }

  /**
   * 根据中支代码获取中支名称
   * @param {string} subBranchCode - 中支代码
   * @returns {string} 中支名称
   */
  getSubBranchNameByCode(subBranchCode) {
    for (const branch of this.branchOptionsBack.value) {
      const subBranch = branch.itemList?.find(item => item.id === subBranchCode)
      if (subBranch) {
        return subBranch.subbranchName
      }
    }
    return ''
  }

  /**
   * 获取所有分公司选项
   * @returns {Array} 分公司选项数组
   */
  getBranchOptions() {
    return this.branchOptions.value
  }

  /**
   * 获取当前中支选项
   * @returns {Array} 中支选项数组
   */
  getSubBranchOptions() {
    return this.subBranchOption.value
  }

  /**
   * 重置所有数据
   */
  reset() {
    this.branchOptions.value = []
    this.branchOptionsBack.value = []
    this.subBranchOption.value = []
    this.showBranchPickerPopup.value = false
    this.showSubBranchPickerPopup.value = false
  }
}

/**
 * 创建分公司和中支管理器实例
 * @returns {BranchDepartmentManager} 管理器实例
 */
export function createBranchDepartmentManager() {
  return new BranchDepartmentManager()
}

/**
 * 默认导出管理器类
 */
export default BranchDepartmentManager